*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::before,*::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h1, h2, h3, h4, h5, h6{
  font-weight: normal;
}
ul,ol{
  list-style: none;
}
a{
  color: inherit;
  text-decoration: none;
}

button, input {
  font: inherit;
}